<template>
	<div class="status">	
		<div class="title">
			<div class="box">	
				<div class="gift">
					<span class="giftmeg">礼物</span>
					<span class="meg">是证明默契的最佳方式>></span>
				</div>
			</div>
		</div>
		<div class="play">	
			<span class="my">我也要玩</span>
		</div>
		<div class="save">
			<span class="wire"></span>
			<span>长按可以保存图片</span>
			<span class="wire"></span>
		</div>	
	</div>
</template>
<script>
	export default{
		name:'status',
		data(){
			return{

			}
		}
	}
</script>
<style lang="less" scoped>
@import "./../style/mixin.less";
.status{
	position: absolute;
		left: 0;
		top: 0;
	 	width: 750*@rem;
	  	height: 1208*@rem;
	  	overflow: hidden;
	  	background: url(../assets/answer-bg.png) no-repeat;
	  	background-size: 100% 100%;
	  	.title{
			position: relative;
			width: 620*@rem; 
			height: 912*@rem;
			background: url(../assets/one-star.png) no-repeat;
			background-size: 100% 100%;
			margin: 47*@rem auto 0;
			margin-bottom: 45*@rem;
			.box{position: absolute; 
				width: 620*@rem; 
				bottom: 0;
			.gift{ height: 85*@rem;
				width: 480*@rem; 
				border:4*@rem dashed#ff278d; 
				bottom: 0; font-size: 27*@rem; 
				margin:0 auto;
				margin-bottom: 60*@rem;
				text-align: center;
				line-height: 85*@rem; 
				border-radius:30*@rem;
				background-color: #fcebf6;
				.giftmeg{ color: #ff278d;font-weight: bold;}
				.meg{ color: #715f64;font-weight: bold;}
				}
	  		}
		}
		.play{width: 248*@rem; 
			height: 80*@rem;
			background-color:#ff278d; 
			margin: 0 auto; 
			border-radius: 10*@rem; 
			font-size: 38*@rem; 
			color: #FFFFFF; 
			text-align: center;
			line-height: 80*@rem;
			.my{font-weight: bold;}
		}
		.save{margin: 0 auto; 
			font-size: 25*@rem; 
			color:#ff84b9;text-align: 
			center; padding-top: 35*@rem;
			.wire{ display: inline-block; 
				width: 66*@rem; 
				height: 2*@rem; 
				background-color:#ff84b9; 
				margin: 0 19*@rem; 
				margin-bottom: 9*@rem;}
		}
}

</style>


